<template>
  <ul class="switches">
    <li class="switch-item" :class="{'active':currentIndex === index}" v-for="(item,index) in switches" :key="index" @click="switchItem(index)">
      <span>{{item.name}}</span>
    </li>
  </ul>
</template>

<script type="text/ecmascript-6">
export default {
  props: {
    switches: {
      type: Array,
      default () {
        return []
      }
    },
    currentIndex: {
      type: Number,
      default: 0
    }
  },
  methods: {
    switchItem (index) {
      // this.currentIndex = index  Note: do not mutate a prop derectly
      this.$emit('onSwitch', index)
    }
  }
}
</script>

<style scoped lang="stylus">
@import '~common/stylus/variable'

.switches
  display flex
  align-items center
  width 240px
  margin 0 auto
  border 1px solid $color-highlight-background
  border-radius 5px

  .switch-item
    flex 1
    padding 8px
    text-align center
    font-size $font-size-medium
    color $color-text-d

    &.active
      background $color-highlight-background
      color $color-text
</style>



