    </router-link>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  data () {
    return {

    }
  },
  components: {

  }
}
</script>

<style scoped lang="stylus">
@import '~common/stylus/variable'
@import '~common/stylus/mixin'

.m-header
  position relative
  height 44px
  text-align center
  color $color-theme
  font-size 0

  .icon
    display inline-block
    vertical-align top
    width 30px
    height 32px
    margin-top 6px
    margin-right 9px
    bg-image('logo')
